<template>
  <section class="py-4">
    <div class="mb-3 d-flex justify-content-between">
      <ul class="list-unstyled">
        <li class="nav-item dropdown">
          <a
            class="nav-link dropdown-toggle"
            href="#"
            role="button"
            data-bs-toggle="dropdown"
            aria-expanded="false"
          >
            篩選
          </a>
          <ul class="dropdown-menu">
            <li><a class="dropdown-item" href="#">Action</a></li>
            <li><a class="dropdown-item" href="#">Another action</a></li>
            <li><hr class="dropdown-divider" /></li>
            <li><a class="dropdown-item" href="#">Something else here</a></li>
          </ul>
        </li>
      </ul>
      <button class="btn btn-dark" @click="this.$emitter.emit('addEditCom', { status: true })">
        新增廠商
      </button>
    </div>
    <table class="table table-striped table-hover">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">公司名</th>
          <th scope="col">負責人</th>
          <th scope="col">電話</th>
          <th scope="col">統編</th>
          <th scope="col">地址</th>
          <th scope="col">合作記錄</th>
          <th scope="col">修改</th>
        </tr>
      </thead>
      <tbody>
        <template v-for="(item, index) in companyList" :key="index">
          <tr>
            <th scope="row">{{ index + 1 }}</th>
            <td>{{ item.companyName }}</td>
            <td>{{ item.name }}</td>
            <td>{{ item.phone }}</td>
            <td>{{ item.taxIdNumber }}</td>
            <td>{{ item.address }}</td>
            <td>
              <button class="btn btn-dark" @click="this.$emitter.emit('cooperList', item._id)">
                <i class="bi bi-journal-text"></i>
              </button>
            </td>
            <td>
              <button
                class="btn btn-success"
                @click="this.$emitter.emit('addEditCom', { status: false, data: item })"
              >
                <i class="bi bi-pencil"></i>
              </button>
            </td>
          </tr>
        </template>
      </tbody>
    </table>
  </section>
</template>

<script>
import { apiGetCompanys } from '@/assets/javascript/api';
import { getToken } from '@/assets/javascript/methods';

export default {
  data() {
    return {
      companyList: [],
    };
  },
  methods: {
    checkSignIn() {
      // 取得 cookie 中的 token
      const token = getToken();

      // 若有 token 則登入，反之轉為登入頁面
      if (token) {
        this.$http.defaults.headers.common.Authorization = `Bearer ${token}`;
      } else {
        this.$router.push('/login');
      }
    },
    getCompanys() {
      this.$emitter.emit('loadingStatus', true);
      apiGetCompanys()
        .then((res) => {
          this.companyList = res.data.data;
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          this.$emitter.emit('loadingStatus', false);
          console.log(err);
        });
    },
  },
  mounted() {
    this.getCompanys();
    this.$emitter.on('getCompanyLsit', () => {
      this.getCompanys();
    });
  },
  created() {
    this.checkSignIn();
  },
};
</script>
